.bike {
  background-color: $grey;
  padding-top: 119px;

  @media (max-width: $desktop-width) {
    padding-top: 26px;
  }
}

.bike h3 {
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
  color: $dark;
  text-align: center;
  margin-bottom: 61px;

  @media (max-width: $desktop-width) {
    font-size: 24px;
    line-height: 41px;
    margin-bottom: 30px;
  }
}

.bike__list {
  max-width: 1222px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-left: 46px;
  padding-bottom: 127px;

  @media (max-width: 1221px) {
    align-items: center;
    justify-content: center;
    padding-left: 0;
    padding-bottom: 48px;
  }

  @media (max-width: $tablet-width) {
    padding-bottom: 32px;
    flex-direction: column;
  }
}

.bike__list-item {
  position: relative;
  margin-right: 50px;

  @media (max-width: $tablet-width) {
    margin-right: 0;
  }
}

.bike__list-item p {
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  color: $black;
  letter-spacing: -0.005em;
  text-align: center;
  margin-top: 16px;
}

.bike__image {
  position: relative;
  z-index: 2;

  @media (max-width: $desktop-width) {
    width: 270px;
    height: 300px;
    object-fit: cover;
    border-radius: 10px;
  }
}

.bike__list-item--road::before {
  content: "";
  position: absolute;
  width: 180px;
  height: 180px;
  background-color: transparent;
  border: 27px solid $blue;
  top: 37%;
  transform: translateY(-50%);
  left: -59px;
  border-radius: 50%;

  @media (max-width: $desktop-width) {
    top: 70%;
    width: 127px;
    height: 127px;
    border-width: 18px;
    left: -39px;
  }

  @media (max-width: $tablet-width) {
    left: -25px;
  }
}

.bike__list-item--mountain {
  margin-top: 29px;

  &::before {
    content: "";
    position: absolute;
    width: 47px;
    height: 47px;
    background-color: transparent;
    border: 8px solid $blue;
    top: -18px;
    left: -19px;
    z-index: 3;
  }

  &::after {
    content: "";
    position: absolute;
    width: 47px;
    height: 47px;
    background-color: transparent;
    border: 8px solid $blue;
    bottom: 53px;
    right: -23px;
    z-index: 3;
  }

  @media (max-width: $desktop-width) {
    margin-top: 0;
    margin-right: 0;

    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }

  @media (max-width: $tablet-width) {
    margin-top: 20px;
  }
}

.bike__list-item--baby {
  margin-top: -10px;

  @media (max-width: $desktop-width) {
    margin-top: 28px;
    margin-left: 50px;
  }

  @media (max-width: $tablet-width) {
    margin-top: 20px;
    margin-left: 0;
  }
}

.bike__list-item--baby::after {
  content: "";
  position: absolute;
  width: 95px;
  height: 95px;
  background-color: transparent;
  border: 17px solid $blue;
  top: -32px;
  right: -36px;
  border-radius: 50%;
  z-index: 3;

  @media (max-width: $desktop-width) {
    width: 68px;
    height: 68px;
    border-width: 13px;
    top: -19px;
    right: -22px;
  }

  @media (max-width: $tablet-width) {
    right: -14px;
  }
}
